body.is-grabbing{
  cursor: -webkit-grabbing;
  -webkit-user-select: none;
}

body.is-scrolling .knob {
  cursor: default;
}
.container {
  display: flex;
  width: 100vw;
  height: 100vh;
  align-items: center;
  justify-content: space-around;
  background: none;
  margin: 0;
}

.knob {
  border: 1px solid #444;
  /* box-sizing: border-box; */
  color: #444;
  display: inline-block;
  width: 50px;
  height: 50px;
  cursor: -webkit-grab;
  opacity: 1;
  border-radius: 100%;
  background-color: white;
  transition: background-color 100ms, opacity 200ms, transform 100ms;
  position: relative;
  /* overflow: hidden; */
  /*box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2); */
}

.knob.is-active {
  transform: scale(1.1);
  cursor: -webkit-grabbing;
}

.knob .inner {
  position: absolute;
  top: -2%;
  width: 8%;
  left: 46%;
  height: 52%;
  display: block;
  background-color: #444;
  transform-origin: center 100%;
  will-change: transform;
}